@import "../variables.scss";

.getBookmark-section {
  @include mx-auto;
  h1 {
    font-size: 2em;
    font-weight: 500;
    margin-right: 3rem;
  }
  .getBookmark-container {
    display: flex;
    position: relative;
    margin-bottom: 12rem;
    margin-top: 9rem;
    .getBookmark-text {
      max-width: 40%;
      p {
        margin-top: 2rem;
      }
      .getBookmark-buttons {
        margin-top: 2.5rem;
        .getBookmark-btn--blue {
          @include blue-btn;
          font-size: 1em;
          padding: 0.9rem 1rem 0.9rem 1rem;
        }
        .getBookmark-btn--blue:hover {
          @include blue-btn--hover;
        }
        .getBookmark-btn--white {
          @include blue-btn;
          font-size: 1em;
          background-color: #f6f6f6;
          color: #8e8f96;
          border: 2px solid #f6f6f6;
          padding: 0.9rem 1rem 0.9rem 1rem;
          margin-left: 1rem;
          box-shadow: 4px 4px 4px 1px rgba(0, 0, 0, 0.1);
        }
        .getBookmark-btn--white:hover {
          color: gray;
          border: 2px solid gray;
          background-color: white;
          transition: 0.3s;
          animation: ease-in-out;
          cursor: pointer;
        }
      }
    }
  }
  @media (max-width: 940px) {
    .getBookmark-container {
      .getBookmark-text {
        margin-top: 20rem;
        max-width: 100%;
        text-align: center;
        h1 {
          font-size: 1.5em;
          margin: 0;
        }
        p {
          margin: 1rem 0 1rem 0;
        }
        .getBookmark-buttons {
          display: flex;
          justify-content: center;
          font-size: 0.8em;
        }
      }
    }
  }
}
.getBookmark-div-absolute {
  position: absolute;
  right: 0;
  top: 15%;
  .absolute-img {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 40;
  }
  .blue-div-right {
    position: absolute;
    right: 0;
    top: 0;
    transform: translate(0, 40%);
    width: 500px;
    height: 325px;
    background-color: $light-blue;
    border-radius: 70rem 0 0 70rem;
  }
}

@media (max-width: 1180px) {
  .getBookmark-div-absolute {
    top: 7.5%;
    .absolute-img {
      width: 600px;
    }
    .blue-div-right {
      width: 400px;
      height: 300px;
      transform: translate(0, 50%);
      background-color: $light-blue;
      border-radius: 70rem 0 0 70rem;
    }
  }
}

@media (max-width: 768px) {
  .getBookmark-section {
    .getBookmark-container {
      .getBookmark-text {
        margin-top: 15rem;
      }
    }
  }
  .getBookmark-div-absolute {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    top: 15%;
    // margin: 0.5rem;
    .absolute-img {
      width: 400px;
      position: relative;
    }
    .blue-div-right {
      height: 200px;
      width: 200px;
      position: static;
      transform: translate(0, -100%);
    }
  }
}

@media (max-width: 500px) {
  .getBookmark-div-absolute {
    top: 10%;
    .absolute-img {
      width: 100%;
      position: relative;
    }
  }
}

@media (max-width: 400px) {
  .getBookmark-div-absolute {
    top: 15%;
  }
}
